.promo {
  background: linear-gradient(to bottom, $green-back 63%, $white 63%, $white 37%),
    url("../img/header/index-background-mobile.png");
  background-repeat: no-repeat;
  background-position-x: center;
  margin-bottom: 20px;
  background-size: 100%;

  @media (min-resolution: 2dppx) {
    background: linear-gradient(to bottom, $green-back 63%, $white 63%, $white 37%),
      url("../img/header/index-background-mobile@2x.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-x: center;
  }

  @media (min-width: $tablet-width) {
    background: $white;
    margin-bottom: -4px;
  }

  @media (min-width: $desktop-width) {
    background: none;
    padding-top: 116px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 50%;
      height: 588px;
      background-color: $green-back;
    }
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  position: relative;

  @media (min-width: $tablet-width) {
    margin-left: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 190px;
    margin-bottom: 86px;
  }
}

.promo__title {
  color: $white;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  font-weight: normal;
  font-style: normal;
  margin: 0;
  padding-top: 26px;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    color: $black;
    font-size: 60px;
    line-height: 60px;
    width: 420px;
    margin-bottom: 41px;
    text-align: left;
    padding: 0;
    margin-left: 57px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 0;
  }
}

.promo__slogan {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  color: $white;
  margin: 0;
  padding: 0;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    color: $black;
    font-size: 20px;
    line-height: 20px;
    width: 420px;
    margin-bottom: 20px;
    text-align: left;
    margin-left: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 51px;
    margin-left: 0;
  }
}

.promo__image {
  margin: 0 auto;
  padding: 0;
  display: block;
  margin-bottom: 3px;

  @media (min-width: $tablet-width) {
    width: 709px;
    height: auto;
    margin-left: -31px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    left: 32%;
    top: -82px;
    width: 552px;
  }
}

.promo__link {
  text-decoration: none;
  color: $white;
  background: $green-base;
  display: block;
  height: 40px;
  margin: 0 20px;
  padding: 0;
  border: none;

  &:hover {
    background: $green-hover;
  }

  &:active span {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    width: 244px;
    height: auto;
    position: absolute;
    top: 230px;
    left: 40px;
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    margin-bottom: 185px;
    position: static;
  }
}

.promo__link-text {
  display: block;
  font-family: "Oswald", sans-serif;
  font-weight: normal;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  padding: 11px 44px;

  @media (min-width: $tablet-width) {
    padding: 17px 18px;
    line-height: 30px;
  }
}
